import Alert from '@mui/material/Alert';
import Grid from '@mui/material/Grid';

export default function CustomAlert({
  type,
  text,
}: {
  type?: string;
  text?: string;
}) {
  let errorMessage = text;

  if (type) {
    switch (type) {
      case 'required':
        errorMessage = '  ';
        break;
      case 'pattern':
        errorMessage = ' ';
        break;
      case 'unauthorized':
        errorMessage = '  ';
        break;
      case 'unregisteredRecipientNumber':
        errorMessage = '  ';
        break;
      default:
        errorMessage = ' ';
        break;
    }
  }

  return (
    <Grid item xs={12}>
      <Alert role="alert" severity="warning">
        {errorMessage}
      </Alert>
    </Grid>
  );
}
